<template>
  <div class="wrap">
    <h1></h1>
    <p>
      <span>1.</span>
      <em>HTML/CSSW3C</em>
    </p>
    <p>
      <span>2.</span>
      <em>JavaScriptjQuery</em>
    </p>
    <p>
      <span>3.</span>
      <em>vue(vue+ vue-router +vuex+ axios +es6 +stylus)</em>
    </p>
    <p>
      <span>4.</span>
      <em>webpack,</em>
    </p>
    <p>
      <span>5.</span>
      <em>Vue2.0 Element, mint, ydui, muse, vux</em>
    </p>
    <p>
      <span>6.</span>
      <em>AngularJSreact,</em>
    </p>
    <p>
      <span>7.</span>
      <em>gitsvn</em>
    </p>
    <p>
      <span>8.</span>
      <em>Xshell</em>
    </p>
    <div class="progree">
      
      <div class="round">
        <yd-progressbar :progress="progress2" trail-width="2" stroke-width="4" stroke-color="#B2B2B2" trail-color="#FE5D51" fill-color="#EAEAEA">{{progress2 * 100}}%</yd-progressbar>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  name: 'skills',
  data () {
    return {
      progress2: 0.9
    }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.wrap
  width 100%
  height 100%
  background #F8F8FF
  p
    padding 0.2rem
    font-size 0.13rem
    line-height 0.2rem
    span
      margin-right 0.1rem
    em
      width 93%
  h1
    text-align center
    font-size 0.2rem
    line-height 0.5rem
    color #8FBC8F
  .progree
    text-align center
    font-size 0.2rem
    color #EE0000
    .round
      width 0.6rem
      height 0.6rem
      margin-left 41%
      margin-top 0.2rem
</style>



