<template>
    <div class="contet">
      <!-- <yd-tab v-model="tab1">
        <yd-tab-panel label=""> -->
          <h2></h2>
          <div class="detail">
            <!-- <yd-cell-group> -->
            <p>
              <yd-cell-item>
                <span class="color" slot="left">
                  <em style="margin-right:.25rem"></em></span>
                <yd-input slot="right" v-model="input7" required :show-clear-icon="false" placeholder=""></yd-input>
              </yd-cell-item>
            </p>
            <p>
              <yd-cell-item arrow>
                <span class="color" slot="left"></span>
                <yd-datetime type="date" v-model="datetime4" slot="right"></yd-datetime>
              </yd-cell-item>
            </p>
            <!-- </yd-cell-group> -->
            <p class="sex">
              <span class="color">
                <em style="margin-right:.25rem"></em>:</span>
              <yd-radio-group v-model="radio2" style="display:inline-table">
                <yd-radio val="1"></yd-radio>
                <yd-radio val="2"></yd-radio>
              </yd-radio-group>
            </p>
            <p class="place">
              <yd-cell-item>
                <span class="color" slot="left">
                  <em style="margin-right:0.25rem"></em>:</span>
                <input slot="right" type="text" @click.stop="show2 = true" v-model="model2" readonly placeholder="">
              </yd-cell-item>
              <yd-cityselect v-model="show2" ref="cityselectDemo" :callback="result2" :items="district" provance="" city="" area=""></yd-cityselect>
            </p>
            <p>
              <yd-cell-item>
                <span class="color" slot="left">:</span>
                <yd-input slot="right" v-model="input8" required :show-clear-icon="false" placeholder=""></yd-input>
              </yd-cell-item>
            </p>
            <p>
              <yd-cell-item arrow>
                <span class="color" slot="left">:</span>
                <yd-datetime type="date" v-model="datetime5" slot="right"></yd-datetime>
              </yd-cell-item>
            </p>
            <p>
              <yd-cell-item>
                <span class="color" slot="left">:</span>
                <yd-input slot="right" v-model="input9" required :show-clear-icon="false" placeholder=""></yd-input>
              </yd-cell-item>
            </p>
            <p>
              <yd-cell-item>
                <span class="color" slot="left">
                  <em style="margin-right:0.06rem"></em>
                  <em style="margin-right:0.06rem"></em>:</span>
                <yd-input slot="right" v-model="input10" regex="mobile" placeholder=""></yd-input>
              </yd-cell-item>
            </p>
            <p>
              <yd-cell-item>
                <span class="color" slot="left">
                  <em style="margin-right:0.25rem"></em>:</span>
                <yd-input slot="right" v-model="input11" :debug="true" regex="email" placeholder=""></yd-input>
              </yd-cell-item>
            </p>
          </div>
        <!-- </yd-tab-panel>
        <yd-tab-panel label=""></yd-tab-panel>
        <yd-tab-panel label=""></yd-tab-panel>
         <yd-tab-panel label=""></yd-tab-panel>
      </yd-tab> -->
    </div>
</template>
<script type="text/ecmascript-6">
import District from 'ydui-district/dist/jd_province_city_area_id'
export default {
  name: 'first',
  data () {
    return {
      input7: '',
      radio2: 1,
      datetime4: '1992-03-20',
      show2: false,
      model2: ' ',
      district: District,
      datetime5: '2016',
      input8: '',
      input9: 'web',
      input10: '15037183341',
      input11: 'y_ning26@163.com'
      // tab1: 0
    }
  },
  methods: {
    result2 (ret) {
      this.model2 = ret.itemName1 + ' ' + ret.itemName2 + ' ' + ret.itemName3
    }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.contet
  background #F8F8FF
  height 100%
  h2
    text-align center
    font-size .2rem
    padding-top .2rem
  .detail
    margin-top .2rem
    font-size .15rem
    color #333
    .sex
      margin-top .15rem
      padding-left .15rem
    .place
      margin-top .12rem
    .color
      color #00CDCD
      margin-right .1rem
</style>



